@import url(https://fonts.googleapis.com/css?family=Lora:400,400i,700,700i&display=swap);
body {
  margin: 0;
  font-family: 'Lora', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 { 
  font-size: 3.75rem;
  text-align: center;
  font-weight: 700;
  color: #fff;
  margin: 0;
}

p { 
  font-size: 1.125rem;
  text-align: center;
  color: #fff;
}
header {
  background-color: #4c8eca;
  padding: 2.5rem 0;
  margin-bottom: 2.5rem;
}

table {
  width: 85%;
  margin: 2.5rem auto;
  border-collapse: collapse;
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 0.625rem;
}

tr.not-found { 
  display: block;
  padding: 20px 0;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

input {
  margin: 0 auto;
  display: block;
  width: 75%;
  font-size: 1rem;
  padding: 1rem;
}
